@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
.memorice__container{display:grid;justify-content:center;grid-template-columns:1fr;grid-template-rows:80px 100px auto auto;height:100%}.memorice__header{background-color:#7295c0;grid-row:1/2;grid-column:1/2;display:flex;justify-content:space-around;align-items:center}.memorice__NavBar{background-color:#546699;margin-left:-40px;margin-right:0px;grid-column:1/2;grid-row:2/3;display:flex;flex-direction:column;justify-content:center;align-items:center}.memorice__button-NavBar{border-radius:10px;background-color:#353e5f;color:#fff;cursor:pointer;font-size:1.5rem;padding:5px}.memorice__button-NavBar :hover{color:#bac1d9}.memorice__nav-Link{width:100vw;color:#fff;border-bottom:1px solid #fff;display:flex;justify-content:space-around;list-style-type:none;list-style:none;text-decoration:none;cursor:pointer}.memorice__header-title{color:#fff}.memorice__header-logo{width:60px;height:60px}.memorice__footer{display:flex;justify-content:space-between;align-items:flex-end;background-color:#7295c0;grid-column:1/2;grid-row:4/5}.memorice__footer div{margin:20px 10px 0px 10px}.memorice__autor{color:#fff;margin-bottom:15px}.memorice__juego-noNavBAr{grid-row:2/4;grid-column:1/2}.memorice__juego-navBar{grid-row:3/4}.memorice__juego{background-color:#353e5f;height:100%;display:flex;flex-direction:column;align-items:center;margin:20px auto 10px auto}.memorice__instructions{background-color:#546699;box-shadow:3px 3px 3px 2px rgba(0,0,0,.4);border-radius:50px;margin-bottom:10px;padding:30px}.memorice__juego-cards{padding:5px;background-color:#546699;border-radius:50px;box-shadow:3px 3px 3px 2px rgba(0,0,0,.4);display:flex;flex-wrap:wrap;justify-content:center;align-items:center}.memorice__card{box-shadow:2px 2px 2px 1px rgba(0,0,0,.4);border-radius:20px;margin:2px;cursor:pointer;display:flex;justify-content:center;background-color:#353e5f;width:90px;height:120px}.memorice__button{margin-top:20px;display:flex;flex-direction:column;align-items:center;justify-content:center;width:80%}.memorice__button :hover{color:gray}.memorice__button-play-again{cursor:pointer;color:#fff;margin-bottom:5px}.memorice__button-title{width:80%;display:flex;justify-content:center}@media(min-width: 482px){.memorice__juego-cards{width:90%}.memorice__instructions{width:90%}}@media(min-width: 525px){.memorice__juego-cards{width:75%}.memorice__instructions{width:75%}}@media(min-width: 630px){.memorice__juego-cards{width:60%;padding:30px}.memorice__instructions{width:60%}}@media(min-width: 1024px){.memorice__NavBar{display:none}.memorice__icon-header{display:none}.memorice__card-img-oculta{background-repeat:repeat !important}.memorice__nav-laptop{display:flex !important}.memorice__nav-laptop-Link{text-decoration:none;font-size:1.5rem;color:#fff;padding:10px}.memorice__nav-laptop-Link:hover{color:gray}.memorice__juego-cards{width:50%}.memorice__instructions{width:50%}.memorice__card{width:150px;height:200px}}@media(min-width: 1075px){.memorice__juego{width:80%}}.memorice__nav-laptop{display:none}.memorice__card-title{text-align:center;color:#fff}.memorice__card-img-oculta{background:url(https://cdn.discordapp.com/attachments/505862455029792788/870412101871607808/marcaagua.png),#fff;background:url(https://cdn.discordapp.com/attachments/505862455029792788/870412101871607808/marcaagua.png),linear-gradient(to left, #353e5f, #7295c0);background-repeat:no-repeat;background-position:center}.memorice__card-img-descubierta{height:100%}.login-conteniner{width:310px;height:100vh;display:flex;margin:auto;flex-direction:column;justify-content:center;align-self:center}.login-conteniner h1,.login-conteniner h2{text-align:center}.login-conteniner h2{color:#5ba339}.login-conteniner input{font-family:"Poppins",sans-serif;color:#353e5f;letter-spacing:5px;font-weight:800;padding:10px;width:285px}.login-conteniner button{cursor:pointer;width:100%;box-shadow:3px 3px 3px 2px rgba(0,0,0,.4);background-color:#7295c0;font-family:"Poppins",sans-serif;letter-spacing:5px;font-weight:1000;color:#fff;margin-top:5px;padding:10px}.login__link{margin-top:5px;color:#fff}.rank__container{width:71%;height:80vh;margin:10px auto 10px auto;background-color:#546699;border-radius:25px;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.rank__table{width:80%;margin-top:20px}.rank__table th{background-color:#353e5f}.rank__table tr:nth-child(odd){background-color:#7295c0}body,html{font-family:"Poppins",sans-serif;color:#fff;background-color:#353e5f}body,html,h1,h2,h3,h4,h5,li{margin:0px}p{color:#e6e6e6}a{color:#fff;text-decoration:none}a:hover{color:#bac1d9}#root{background-color:#353e5f}.dispay-none{display:none}
